<script>
import { searchLoad, getSelectBoxTagList } from '@/http/service/search'

import ProductList from '@/components/publicProductList/index'
export default {
  name: 'guessYouLick',
  props: {
    // 默认是否显示没有更多啦~
    more: {
      type: Boolean,
      default: false
    },
  },
  components: {
    publicProductList: ProductList
  },
  data() {
    return {
      isMore: this.more,
      guessList: [],
      activityTabs: [], // 活动标签配置
    }
  },
  filters: {
    /**
     * 价格保留两位小数
     */
    toFixedTwo (number) {
      const price = number ? number.toFixed(2) : '0.00';
      return price;
    },
  },
  watch: {
    more: {
      handler(newVal, oldVal) {
        this.isMore = newVal
      }
    }
  },
  created() {
    // 初始化活动标签信息配置
    this.getSelectBoxTagList()
  },
  mounted() {
    let locationInfo = this.$store.state.locationInfo
    searchLoad({
      provinceId: locationInfo.provinceId,
      cityId: locationInfo.cityId,
      districtId: locationInfo.districtId,
      streetId: locationInfo.townshipId,
      storeId: sessionStorage.getItem('shareUserId') || '', // 分享人
      firstPage: '', // 是否第一页，不传为第一页
      productIds: ''// 商品id拼接字符串，如不传，则返回平台推荐商品
    }).then(res => {
      if (res.success) {
        console.log('guesslike', res)
        this.guessList = res.data.productList.slice('0', '6')
        this.guessList.forEach(item => {
          item.finalPrice = item.lowestFlashPrice || item.finalPrice
          item.isLowestFlash = !!item.lowestFlashPrice
          // 活动标签 处理逻辑
          this.activityTabs.forEach(_item => {
            if (_item.id === item.actTagId) {
              item.productAct = _item.tagName
              item.productActTerm = _item.actDate
            }
          })
        })
      } else {
        this.$toast(res.message)
      }
    })
  },
  methods: {
    /**
     * 获取活动标签
     */
    getSelectBoxTagList() {
      getSelectBoxTagList().then(res => {
        let result = res.data;
        this.activityTabs = result || []
      })
    },
    /**
     * @description  点击跳转到商品详情
    */
    toGoodsDetail(productId) {
      const context = this;
      context.$router.push({
        path: `/goodsDetail?productId=${productId}`,
      });
    },
  }
}
</script>

